import React, { Component } from 'react';

function NumberList(props) {
  const numbers = props.numbers;
  const listItems = numbers.map((number) =>
    <li key={number.toString()}>{number}</li>
  );
  return (
    <ul>{listItems}</ul>
  );
}

class Results extends Component {
  constructor(props) {
    super(props);
    this.state = {
      bpm: 0,
      noteDuration: 0,
      startPosition: [],
      windowStartPosition: []
    };
  }

  render() {
    return (
      <div>
        <p>BPM: {this.props.bpm}</p>
        <p>note duration: {this.props.noteDuration} msec.</p>
        <p>Position in percentage</p>
        <NumberList numbers={this.props.startPosition} />
        <p>Position with Granular window offset</p>
        <NumberList numbers={this.props.windowStartPosition} />
      </div>

    );
  }

}

export default Results;


